@import '../../scss/variables.scss';

.profile{
	display: flex;
	flex-direction: column;
	align-items: center;
   max-width:1224px;
	min-height: 100vh;
	padding: 112px 24px 0px 24px;
	margin: 0 auto;

	@media screen and (max-width: 500px) {
		padding: 112px 16px 0 16px;
	}

	&__photo{
	width: 250px;
   height: 250px;	
	}

 img {	
	width: 100%;
	height: 100%;
	border-radius: 50%;
	 object-fit: cover;
 }

 &__title{
	margin-top: 16px;
   display: flex;
   flex-direction: column;
   align-items: center;
   position: relative;	
 }
 &__name{
	color: #fff;
	font-size: 32px;

	@media screen and (max-width: 500px) {
		font-size: 24px;
  }
	span {
		//color: #8c12f0;
		color: $pink;
  }
 }
 &__job{
	color: #918d8d;   
   font-size: 20px;
 }

 &__text{
	margin-top: 32px;
   color: #fff;
   text-align: center;
   font-size: 80px;
   line-height: 1.3;

	@media screen and (max-width: 800px) {
		font-size: 56px;		
  }
  @media screen and (max-width: 530px) {
	font-size: 40px;
} 
 }

 a{
	margin-top: 48px;
   text-decoration: none;
   color: #fff;
   background: $pink;
   padding: 10px 16px;	
   border-radius: 4px;
   font-size: 14px;   
 }

&__web, &__uiux{
	position: absolute;
   background: rgba(255,255,255,0.15);
   color: #fff;
   padding: 9px 16px;
   border-radius: 4px;
   font-size: 15px;
   cursor: pointer;

	@media screen  and (max-width: 800px) {		
			 display: none;		
  }
 }


 &__web{
	right: 25px;
   top: 56px;
 }

 &__uiux{
	top: 56px;
   left: 32px;
}

}